/* src/editor.css */

.wp-block-aurora-design-blocks-related-posts-block {
  border: 2px dashed #007cba; /* 青い点線枠 */
  padding: 15px;
  margin: 10px 0;
  background-color: #f0f8ff; /* 薄い青 */
}

/* プレースホルダー/ガイドメッセージのスタイル */
.wp-block-aurora-design-blocks-related-posts-block .adb-related-guide {
  padding: 10px;
  background-color: #e6f3ff;
  border-radius: 5px;
  text-align: center;
  font-size: 14px;
  color: #007cba;
  font-weight: bold;
}

.wp-block-aurora-design-blocks-related-posts-block .adb-related-guide p {
  margin-top: 5px;
  font-size: 12px;
  color: #32373c;
  font-weight: normal;
}
